* {
  font-family: sans-serif;
}

body {
  margin: 0;
  background: linear-gradient(135deg, #94eaec, #7c9aea, #61d8fc);
  background-size: 300% 300%;
  animation: gradientAnimation 15s ease infinite;
  background-attachment: fixed;
  /* Add this line */
}

